import DBF from './dbFactory'

export default DBF.context;

let prefix = 'https://world-elite.site'

DBF.create('api', {
    //home  
    getPicUrls: {
        url       : prefix+'/api/get_pic_urls',
        method    :'GET',
    },
    //home 
    sendComment: {
        url       : prefix+'/api/comment',
        method    :'GET',
    },
    //register 
    accountLogin: {
        url       : prefix+'/api/account_login',
        method    :'POST',
    },
    //register 
    accountRegister: {
        url       : prefix+'/api/account_register',
        method    :'POST',
    },
    //register/detailinfo 
    setDetailInfo: {
        url       : prefix+'/api/set_detailInfo',
        method    :'POST',
    },
    
});



